import React from 'react'
import classnames from 'classnames'
import ActionSheet from '../../../src/ActionSheet'
import Button from '../../../src/Button'

class AlertDemo extends React.Component {
  constructor() {
    super()
    this.state = {
      show: false
    }
    this.handleClose = this.handleClose.bind(this)
    this.handleShow = this.handleShow.bind(this)

  }

  handleClose() {
    this.setState({
      show: false
    })
  }

  handleShow() {
    this.setState({
      show: true
    })
  }

  render() {
    const items = [
      <p></p>,
      <p></p>,
      <p onClick={this.handleClose} className='cancel'></p>
    ]
    return (
      <section className='page-actionsheet-demo'>
        <div className="page--header">
          <h1 className="page-title">ActionSheet</h1>
          <p className="page--desc"></p>
        </div>
        <Button onClick={this.handleShow}>ActionSheet</Button>
        <ActionSheet
          show={this.state.show}
          menus={items}
          onClose={this.handleClose}
          isClickMaskToClose
        />
      </section>
    )
  }
}

export default AlertDemo



